.btn.loading,.btn.loading:hover{
	pointer-events: none;
}
.btn.loading:before{
	border-radius: 9999px;
	border-width: 2px;
	height: 1rem;
	margin-right: .5rem;
	width: 1rem;
	animation: spin 2s linear infinite;
	content: "";
	border-color: transparent currentColor currentColor transparent;
}
