}
.checkbox:focus{
	outline: 2px solid transparent;
	outline-offset: 2px;
}
.collapse{
	display: grid;
	overflow: hidden;
	position: relative;
	outline: 2px solid transparent;
	outline-offset: 2px;
}
.collapse-content,.collapse-title,.collapse>input[type=checkbox]{
	grid-column-start: 1;
	grid-row-start: 1;
}
